






































































































import { Component, Vue } from "vue-property-decorator";

@Component({})
export default class Step1 extends Vue {
  selected = "";

  mounted() {
    this.selected = this.$route.params.s1;
  }

  goToNextStep() {
    this.$router.push({ name: 'step2', params: { s1: this.selected } });
  }
}
